<template>
  <jroll-infinite3 class="scroll-wrapper" id="wrapper3">
    <div class="jp-evaluation">
      <div  class="listWrap" v-if="ments === []">....</div>
      <div class="listWrap" v-for="item in ments">
        <div class="list-title">
          <span class="userName">{{item.mobile}}</span>
          <v-start :score="item.score"></v-start>
          <small >{{item.score}}.0</small>
          <small class="jp-time">{{item.add_time}}</small>
        </div>
        <p class="listEva">
          {{item.comment}}
        </p>
        <div class="listState">
          {{item.borrow_status}}
        </div>
      </div>
    </div>
  </jroll-infinite3>
</template>

<script type="text/ecmascript-6">
  //  
  import stars from '../common/stars.vue'

  var _this3 = ''
  export default {
    name: 'userMent',
    data () {
      return {
        id : this.$route.params.id,
        p : this.$route.params.p,
        ments : [],
        t_page : '',
        nexturl : ''
      }
    },
    created(){
      _this3 = this

      this.$http.post('http://'+this.$store.state.urlIp+'api/index/borrow_comment/id/'+this.id,{label:1}).then(response=>{

        this.ments = response.body.info
        this.t_page = response.body.t_page
        this.nexturl = response.body.nexturl
        //
        var jroll = new JRoll("#wrapper3");
        //do somethingscrollerscroller
        if (this.ments.length === 0) {
          jroll.disable()
        }
      })
    },
    activated(){

      var jroll = new JRoll("#wrapper3");
      //do somethingscrollerscroller
      jroll.refresh();
    },
    components:{
      'v-start' : stars,
      'jroll-infinite3': JRoll.VueInfinite({
        tip: '<img class="homeLoding" src="./static/img/001.gif" >...</img>',
        pulldown: {
          refresh: function(complete) {
            //complete
            complete();
          }
        },
        bottomed: function (complete) {
          var me = this
          var page = _this3.t_page  //

          if(typeof complete === 'function'){
            me.tip = '&nbsp;'
            page = 2
            _this3.ments = []
            _this3.nexturl = 'http://'+_this3.$store.state.urlIp+'api/index/borrow_comment/id/'+_this3.id+'/p/1'
          }
          if (me.page <  page - 1) {

            _this3.$http.post(_this3.nexturl,{label:1}).then(response => {
              let res = response.body
              let jons = res.info
              _this3.nexturl = res.nexturl
              if (typeof complete === 'function') {
                _this3.ments =_this3.ments.concat(jons)
                me.page = 0

                complete()
              }else{
                me.page++
                _this3.ments = _this3.ments.concat(jons)
                me.tip = '&nbsp;'
              }
            })
          }
          //
          if(_this3.ments.length === 0 ){
            me.tip = '&nbsp;'
          }else {
            if (_this3.nexturl ===  '' ) {
              me.tip = ''
            }else{
              me.tip = '<img class="homeLoding" src="./static/img/001.gif" >...</img>'

            }
          }
        }
      }, {
        scrollBarY: false
      })
    },

  }
</script>


<style lang="stylus" rel="stylesheet/stylus">
</style>



