import Vue from 'vue'
import Router from 'vue-router'
import store from '../style/js/store'

import Home from 'components/home/home'
import ListWrap from 'components/home/listWrap'

import Commodity from 'components/product/commodity'
import productInfo from 'components/product/productInfo'
import UserMent from 'components/product/UserMent'
import addMent from 'components/product/addMent'


import Personal from 'components/personal/personal'
import CreditInfo from 'components/personal/creditInfo'
import UserStatus from 'components/personal/userStatus'
import Collection from 'components/personal/collection'
import Help from 'components/personal/help'
import AboutUs from 'components/personal/aboutUs'

import Login from 'components/login/login'
import Register from 'components/login/register'
import Findpwd from 'components/login/findPwd'

//url
import Embed from 'components/product/embed'


//
Vue.use(Router)

const title = ''
const router = new Router({
	mode: 'hash',
//linkActiveClass: 'active', // class
  routes: [
    {
      path: '/',
      name: 'Home',
      meta: {
        title: '',
        auth: false,
        keepAlive: true   // false
      },
      component: Home
    },
    {
    	path: '/ListWrap',
    	name: 'ListWrap',
    	meta: {
        	title: '',
        	auth: false,
          keepAlive: false
      	},
      	component: ListWrap
    },
    {
      path: '/commodity',
      name: 'Commodity',
       meta: {
        title: '',
        auth: false,
         keepAlive: true
      },
      component: Commodity
    },
    {
      path: '/productInfo/:id',
      name: 'ProductInfo',
      meta: {
        title: '',
        auth: false,
        keepAlive: false
      },
      component: productInfo

    },
    {
      path: '/UserMent/:id/:p',
      name: 'UserMent',
      meta: {
        title: '',
        auth: false,
        keepAlive: false
      },
      component: UserMent
    },
    {
      path: '/addMent/:id',
      name: 'AddMent',
      meta: {
        title: '',
        auth: false,
        keepAlive: false
      },
      component: addMent
    },
    {
      path: '/embed/:u',
      name: 'Embed',
      meta: {
        title: '',
        auth: false,
        keepAlive: true
      },
      component: Embed
    },
    {
      path: '/creditinfo',
      name: '',
       meta: {
        title: '',
        auth: false,
         keepAlive: false
      },
      component: CreditInfo
    },
    {
      path: '/userstatus',
      name: 'UserStatus',
       meta: {
        title: '',
        auth: false,
         keepAlive: false
      },
      component: UserStatus
    },
    {
      path: '/collection',
      name: 'Collection',
       meta: {
        title: '',
        auth: false,
         keepAlive: false
      },
      component: Collection
    },
    {
      path: '/help',
      name: 'Help',
       meta: {
        title: '',
        auth: false,
         keepAlive: false
      },
      component: Help
    },
    {
      path: '/aboutus',
      name: 'AboutUs',
       meta: {
        title: '',
        auth: false,
         keepAlive: false
      },
      component: AboutUs
    },


    {
      path: '/personal',
      name: 'Personal',
      meta: {
        title: '',
        auth: true,
        keepAlive: false
      },
      component: Personal
    },
    {
      path: '/login',
      name: 'Login',
      meta: {
        title: '',
        auth: false,
        keepAlive: false
      },
      component: Login
    },
    {
      path: '/register',
      name: 'Register',
      meta: {
        title: '',
        auth: false,
        keepAlive: false
      },
      component: Register
    },
    {
      path: '/findpwd',
      name: 'Findpwd',
      meta: {
        title: '',
        auth: false,
        keepAlive: false
      },
      component: Findpwd
    }
  ]
})

// beforeEach
router.beforeEach((to, from, next) => {
  let titleStr = ''
  let con=''

  // auth truefalsetrue
  var { auth = true } =  to.meta
  var isLogin = Boolean(store.state.user.token) //true false

  if (auth && !isLogin && to.path !== '/product') {

    return next({ path: '/login' })
  }

  // 
  next()

  // 
  if (to.name !== 'Home') {
    // title
    for (let i = to.matched.length - 1; i >= 0; i--) {
      con = `${to.matched[i].meta.title}`
      if(i=0){
        con === `${to.matched[0].meta.title}`
      }
      titleStr += con
    }
  }

  // 
  titleStr += title
  // title
  document.title = titleStr

})

export default router





